.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  color : white;
  background-color: black;
  font-size: 50px;
 
  align-items : center;
  padding : 20px;
}

.list {
  padding-left : 20px;
  text-align : left;
  border-bottom : 1px solid grey;

}

.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;

}

.input{
  border : 1px solid gold;
  
  margin : 10px;
  height: 50px;
  width: 30%;
}



